import { SavedFormsItem } from '..';
import './SavedFormsList.scss';

const SavedFormsList = () => {
    const initialForms = [
        {
            title: '  #',
            subtitle: ' ',
            date: '12.12.2021',
        },
        {
            title: '  #',
            subtitle: ' ',
            date: '13.12.2021',
        },
        {
            title: '  #',
            subtitle: ' ',
            date: '14.12.2021',
        },
    ];

    return (
        <ul className='saved-forms-list'>
            {initialForms.map((item, index) => (
                <SavedFormsItem index={index} item={item} />
            ))}
        </ul>
    );
};

export default SavedFormsList;
