import {
    ADD_ELEMENT,
    LABLE_CHANGE,
    REMOVE_ELEMENT,
    SET_REQUIRED,
    SORT_ELEMENTS,
    VALUE_CHANGE,
} from '../types';
import { IFormState, IDragAndDropActions } from '../../interfaces';

const initialState: IFormState = {
    elements: [
        {
            id: 1,
            title: '',
            type: 'title',
            value: '',
            isDisabled: true,
        },
        {
            id: 2,
            title: ' ',
            type: 'header',
            value: '',
            isDisabled: true,
        },
        {
            id: 3,
            title: '',
            type: 'subtitle',
            value: '',
            isDisabled: true,
        },
        {
            id: 4,
            title: '  ',
            type: 'text',
            value: '',
            isDisabled: true,
            isRequired: true,
        },
        {
            id: 5,
            title: '  ',
            type: 'textArea',
            value: '',
            isDisabled: true,
            isRequired: true,
        },
        {
            id: 6,
            title: ' ',
            type: 'number',
            value: '',
            isDisabled: true,
            isRequired: true,
        },
        {
            id: 7,
            title: 'E-mail ',
            type: 'email',
            value: '',
            isDisabled: true,
            isRequired: true,
        },
        {
            id: 8,
            title: '   (checkbox)',
            type: 'checkbox',
            value: false,
            isDisabled: true,
            isRequired: true,
        },
    ],
    constructor: [],
};

export const formReducer = (
    state = initialState,
    action: IDragAndDropActions
): IFormState => {
    switch (action.type) {
        case SORT_ELEMENTS:
            return { ...state, constructor: action.payload };
        case ADD_ELEMENT:
            return { ...state, constructor: action.payload };
        case LABLE_CHANGE:
            return { ...state, constructor: action.payload };
        case VALUE_CHANGE:
            return { ...state, constructor: action.payload };
        case REMOVE_ELEMENT:
            return { ...state, constructor: action.payload };
        case SET_REQUIRED:
            return { ...state, constructor: action.payload };
        default:
            return state;
    }
};

export type FormAction = IDragAndDropActions;
